import React, { useState, useEffect } from 'react';
import './PageStyles.css';

const Home = () => {
  const data='[ "Work in progress."]'// "Hey, I\'m Daniel.", "I\'m a front-end developer.", "I love to design <br> and develop creative UI solutions.", "Let\'s work together."]'
  let timeouts = [];

  class TxtType {
    constructor(el, toRotate, period) {
      this.timeout =0;
      this.toRotate = toRotate;
      this.el = el;
      this.loopNum = 0;
      this.period = parseInt(period, 10) || 2000;
      this.txt = '';
      this.tick();
      this.isDeleting = false;
    }
    tick() {
      var i = this.loopNum % this.toRotate.length;
      var fullTxt = this.toRotate[i];
  
      if (this.isDeleting) {
        this.txt = fullTxt.substring(0, this.txt.length - 1);
      } else {
        this.txt = fullTxt.substring(0, this.txt.length + 1);
      }
  
      this.el.innerHTML = '<span class="wrap">' + this.txt + '</span>';
  
      var that = this;
      var delta = 150 - Math.random() * 100;
  
      if (this.isDeleting) { delta /= 2; }
  
      if (!this.isDeleting && this.txt === fullTxt) {
        delta = this.period;
        this.isDeleting = true;
      } else if (this.isDeleting && this.txt === '') {
        this.isDeleting = false;
        this.loopNum++;
        delta = 500;
      }
  
      timeouts.push(setTimeout(function () {
        that.tick();
      }, delta));
    }
  }


  const startTypeWrite = () => {
    var elements = document.getElementsByClassName('typewrite');
    for (var i = 0; i < elements.length; i++) {
      var toRotate = elements[i].getAttribute('data-type');
      var period = elements[i].getAttribute('data-period');
      if (toRotate) {
        new TxtType(elements[i], JSON.parse(toRotate), period);
      }
    }
  };

  useEffect(() => {
    startTypeWrite()
    console.log(timeouts)

    return () => {
      for(const timeout in timeouts) {
      clearTimeout(timeout);}
      console.log(timeouts)
    }
  }, [])

  return (
    <div className="Page-body">
      <div className="Page-content-container">
        <h1 href="" className="typewrite" data-period="2000" data-type={data}>
          <span className="wrap"></span>
        </h1>
      </div>
    </div>
  );
}

export default Home;